/* Manrope regular */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  //font-display: optional;
  src: url("../fonts/manrope_regular.woff2") format("woff2"),
    url("../fonts/manrope_regular.woff") format("woff");
}

/* Manrope medium */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  //font-display: optional;
  src: url("../fonts/manrope_medium.woff2") format("woff2"),
    url("../fonts/manrope_medium.woff") format("woff");
}

/* Manrope bold */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  //font-display: optional;
  src: url("../fonts/manrope_bold.woff2") format("woff2"),
    url("../fonts/manrope_bold.woff") format("woff");
}